import React from 'react';
import axios from "axios";
class Navbar extends React.Component {
    constructor(props) {
        super(props)
        this.setStateFilms = this.setStateFilms.bind(this)
        this.state = {
            films: [],
            counter: 0, theloai1: null,
        }
    }

    setStateFilms = (data) => {
        console.log(data)
        this.setState({ films: data, counter: 1 })
    }
    setStateType = (data) => {
        this.setState({ theloai1: data })
    }
    handleOnclickFilm = (tenphim) => {
        sessionStorage.setItem("tenphim", tenphim);
    }

    findByTheLoaiPhim = (theloai) => {
        const theloaiphim = {
            TheLoai: theloai
        };
        axios.post("http://conallserver.ddns.net:8000/film/find", theloaiphim)
            .then((res) => {
                if (!res.data.error) {
                    console.log("theloai", res.data.film);
                } else {
                    return window.alert(res.data.error);
                }
            })
    }

    render() {
        if (this.props.films[0] && this.state.counter === 0) {
            this.setStateFilms(this.props.films)
        }
        if (this.props.theloai[0] && this.state.theloai1 === null)
            this.setStateType(this.props.theloai)


        return (
            <div className="htv-nav">
                <div className="container">
                    <nav>
                        <ul className="primary-nav">
                            <li>
                                <a href="/">Lch chiu</a>
                            </li>
                            <li className="sub-nav">
                                <a href="/">Phim</a>
                                <div id="sub-menu">
                                    <ul className="undefined">
                                        <li>
                                            <a href="/">Phim ang chiu</a>
                                        </li>
                                        <li>
                                            <a href="/">Phim sp chiu</a>
                                        </li>
                                    </ul>
                                </div>

                                <div id="sub-menu-movie" className="hidden-sm hidden-xs">
                                    <div className="visible-lg-block">
                                        <div className="row submenu-title">
                                            <div className="col-sm-12">
                                                <h3><a href="phim-dang-chieu">Phim ang chiu</a></h3>
                                                {/* <h3><Link to ={'/allfilm', <TabMovie films={this.state.films}/>}>Phim ang chiu</Link></h3> */}
                                            </div>
                                        </div>
                                        <div className="row movies-group-header">
                                            {this.state.films.map((item, index) =>
                                                ((Date.parse(item["NgayChieu"]) <= Date.parse(Date())) && (Date.parse(Date()) < (Date.parse(item["NgayKetThuc"])))) ?
                                                    <div className="col-sm-3">
                                                        <article className="article-movie-header">
                                                            <img style={{ width: '100%', height: '100%' }}
                                                                key={index}
                                                                src={"http://conallserver.ddns.net:8000/images/" + item.AnhBia} />

                                                            <a style={{ width: '100%' }}
                                                                href="/detailfilm"
                                                                onClick={this.handleOnclickFilm.bind(this, item.TenFilm)}>
                                                                <figure>
                                                                    <figcaption className="overlay">
                                                                        <div className="movies-content-header">
                                                                            <htv-icon-movie />
                                                                            <div className="group">
                                                                                <div className="btn secondary-white btn-header">Mua v</div>
                                                                            </div>
                                                                        </div>
                                                                    </figcaption>
                                                                </figure>
                                                            </a>
                                                        </article>
                                                        <div className="title-movie-header">
                                                            <h4 className="upper-text">{item.TenFilm}</h4>
                                                        </div>
                                                    </div>
                                                    :
                                                    null
                                            )}
                                        </div>

                                        <div className="row submenu-title">
                                            <div className="col-sm-12">
                                                <h3>
                                                    <a href="/">Phim sp chiu</a>
                                                </h3>
                                            </div>
                                        </div>
                                        <div className="row movies-group-header">
                                            {this.state.films.map((item, index) =>
                                                (Date.parse(item["NgayChieu"]) > Date.parse(Date())) ?
                                                    <div className="col-sm-3">
                                                        <article className="article-movie-header">
                                                            <img style={{ width: '100%', height: '100%' }}
                                                                key={index}
                                                                src={"http://conallserver.ddns.net:8000/images/" + item.AnhBia} />
                                                            <a style={{ width: '100%' }}
                                                                href="/detailfilm"
                                                                onClick={this.handleOnclickFilm.bind(this, item.TenFilm)}>
                                                                <figure>
                                                                    <figcaption className="overlay">
                                                                        <div className="movies-content-header">
                                                                            <htv-icon-movie />
                                                                            <div className="group">
                                                                                <div className="btn secondary-white btn-header">Mua v</div>
                                                                            </div>
                                                                        </div>
                                                                    </figcaption>
                                                                </figure>
                                                            </a>
                                                        </article>
                                                        <div className="title-movie-header">
                                                            <h4 className="upper-text">{item.TenFilm}</h4>
                                                        </div>
                                                    </div>
                                                    :
                                                    null
                                            )}
                                        </div>
                                    </div>
                                </div>
                            </li>

                            <li className="sub-nav">
                                <a >Th loi phim</a>
                                <div id="sub-menu">
                                    <ul>
                                        {/* <li>
                                            <a href="/">Phim aventure</a>
                                        </li>
                                        <li>
                                            <a href="/">Phim hnh ng</a>
                                        </li>
                                        <li>
                                            <a href="/">Phim b n</a>
                                        </li>
                                        <li>
                                            <a href="/">Phim hi</a>
                                        </li>
                                        <li>
                                            <a href="/">Phim lng mn</a>
                                        </li> */}
                                        {
                                            this.state.theloai1 !== null ? this.state.theloai1.map(i => (
                                                <li>
                                                    <a onClick={this.findByTheLoaiPhim.bind(this, i)}>{i}</a>
                                                </li>
                                            )) : null
                                        }
                                    </ul>
                                </div>
                            </li>
                            <li className="sub-nav">
                                <a href="/">in nh</a>
                                <div id="sub-menu">
                                    <ul>
                                        <li><a href="/">o din</a></li>
                                        <li><a href="/">Din vin</a></li>
                                        <li><a href="/">Tin tc phim</a></li>
                                    </ul>
                                </div>
                            </li>
                            {/* <li className="sub-nav">
                                <a href="/">H thng rp</a>
                                <div id="sub-menu">
                                    <ul>
                                        <li><a href="/">HTV Th c</a></li>
                                        <li><a href="/">HTV G Vp</a></li>
                                        <li><a href="/">HTV Quang Trung</a></li>
                                        <li><a href="/">HTV Nguyn Hu</a></li>
                                    </ul>
                                </div>
                            </li> */}
                            <li><a href="/">Gi v</a></li>
                            <li><a href="/">Thnh vin</a></li>
                        </ul>
                    </nav>
                </div>
            </div>
        );
    }
}
export default Navbar;