import React, { Component } from 'react';
import './DateTime.css'
class DateTime extends Component {
    constructor(props) {
        super(props);
        var today = new Date();
        var day = today.getDay();
        switch(day) {
            case 0:
                day="Ch Nht"
                break;
            case 1:
                day="Th hai"
                break;
            case 2:
                day="Th ba"
                break;
            case 3:
                day="Th t"
                break;
            case 4:
                day="Th nm"
                break;
            case 5:
                day="Th su"
                break;
            case 6:
                day="Th by"
                break;
            default:
                day="Li"
          }
        // var date= day+ ', '+today.getDate() + '/' + (today.getMonth() + 1) + '/' + today.getFullYear()+'   (UTC+ ' + today.getUTCHours()+')';
        var date= day+ ', '+today.getDate() + '/' + (today.getMonth() + 1) + '/' + today.getFullYear()
        this.state={
           datenow:date
        } 
    }
  
    render() {
        return (
            
                <h5 id="date_now-h5">{this.state.datenow}</h5>
          
        );
    }
}
export default DateTime;